@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --background: #f5f5f5;
  --foreground: #171717;
}

/* @media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
} */

body {
  color: var(--foreground);
  background: var(--background);
  font-family: Arial, Helvetica, sans-serif;
  min-height: 100vh;
}

@layer utilities {
  .text-balance {
    text-wrap: balance;
  }
}

.divide-x > :not([hidden]) ~ :not([hidden]) {
  --tw-divide-x-reverse: 0;
  border-right-width: 0;
  border-left-width: 0;
}



::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, .5);
  box-shadow: 0 0 1px rgba(255, 255, 255, .5);
}

div#main {
  position: relative;
}

a {
  text-decoration: none !important;
}

.header {
  height: 135px;
  background-color: rgb(255, 200, 36);
}



.header_account_name {
  background-color: rgb(14,31,64);
  width: 100%;
}

iframe#launcher {
  position: fixed;
    top: unset;
    bottom: 0;
}

h1.project_name {
  font-size: 2rem;
  color: white;
}


.material-symbols-outlined {
  color: #5E6469;
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 24
}

body {
  overflow: scroll;
}

.calculator-box-container {
  color: white;
}

.calculator-box {
  height: 100%;
  position: relative;
}

.calculator-box-m {
  margin: 0 auto;
  margin-bottom: 1rem;
}

.calculator-box-w {
  max-width: 480px;
}

.calculator-box > p {
  font-weight: bolder;
}

.calculator-box-body {
  min-height: 300px;
}
.calculator-box-body > a {
  color: white;
    font-size: 18px;
    border: solid 1px;
    padding: 1rem;
}

.open-calc {
  border: solid 1px;
  padding: 1rem;
}

.open-calc > a{
  color: white;
    font-size: 18px;
}

.calc-link {
  color: white;
  font-size: 18px;
  border: solid 1px;
  position: relative;
  width: 135px;
  height: 55px;
  padding-top: 0.8rem;
  padding-left: 1rem;
  margin: 0 auto;
  bottom: 25px;
}

.calc-link > a {
  color: white
}

.calculator-box-body > div {
  bottom: 25px;
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}
.calculator-box-body > div > a {
  font-size: 16px;
  color: white;
  line-height: 30px;
  border: solid 1px;
  padding: 1rem 1.5rem 1rem 1rem;
  width: 175px !important;
  position: relative;
  font-weight: bolder;
}

.calculator-box-body > div > a > span {
  position: absolute;
  right: -23px;
  top: -13px;
}

.calc-arrow {
  color: white;
  line-height: 14px;
  position: absolute;
  margin-top: 1.7rem;
  width: 2rem !important;
  right: -8px;
  margin-right: 1rem;
  padding-right: 1rem;
  top: -11px;
}


.calculator-box > h1 {
  font-size: 18px;
  color: white;
  text-align: center;
  margin-bottom: 14px;
  text-transform: uppercase;
  font-weight: bolder;
  letter-spacing: 4px;
  margin-bottom: 1rem;
}
.calculator-box > svg {
  margin: 0 auto;
  width: 74px;
}

.calculator-modal {
  width: 90%;
  height: 100vh;
  position: absolute;
  background: white;
  z-index: 9;
}

li#chat {
  width: 55px;
}


.Spreadsheet__table > tbody > tr > td {
  font-size: 16px;
  line-height: 55px;
}
.popover-custom:hover {
  border: none !important;
  background: none !important;
  border-radius: unset !important;
}
.popover-custom:focus-visible {
  border: none !important;
  background: none !important;
  border-radius: unset !important;
}
.popover-custom:focus {
  border: none !important;
  background: none !important;
  border-radius: unset !important;
}

.project-popover {
  max-width: calc(100vw - 245px);
}

.main-body {
  margin: 0 auto;
  margin-top: 2rem;
  min-height: 100vh;
}

@media screen and (max-width: 767px) {
  .project_main {
    max-height: 300px;
  }

  .project-nav {
    margin-bottom: 1rem;
  }
  .main-body {
    position: relative !important;
    margin-top: 0;
    top: 0 !important;
    
  }
  .prod-well-box {
    padding-bottom: 0.5rem;
  }

  .project-popover {
    left: -75px !important;
    width: 100vw !important;
    max-width: unset !important;
  }

  .project-menu-item {
    margin: 0 auto;
  }

  .mobile-base-well {
    padding: 0.5rem;
  }

  .client-p-well-data {
    padding: 0 !important;
  }
  .client-p-well-data > div{
    padding: 0;
    width: 100%;
  }
  .news-modal-btn {
    padding: 3.7rem 0 0rem 0 !important;
    display: block !important;
  }

  .news-modal-close {
    top: 0 !important;
    padding-top: 1rem !important;
  }

  .default-select {
    width: 100% !important
  }
  .news-modal > div {
    width: 100vw;
  }
  .news-modal > div > div {
    width: 100%;
    padding: 0;
  }
  .news-modal > div > div > div {
    padding: 0;
    width: 100%;
    position: absolute;
    top: 0;
    height: 100vh;
    border-radius: unset;
  }

  .news-modal > div > div > div > iframe {
    height: 100% !important;
  }

  .header_account_name {
    width: 100%;
    display: none;
  }

  #navbar-default {
    padding-right: 0 !important;
  }

  .projects-footer {
    position: relative !important;
  }
  .project-main {
    height: calc(100vh - 100px) !important;
  }

  .logo-forward {
    display: none;
  }
  /* iframe#launcher {
    position: absolute !important;
    top: 35px;
    bottom: unset !important
  } */
  footer {
    padding-top: 0 !important;
    margin-top: 0 !important;
  }
}

#headlessui-portal-root {
  position: relative !important;
  z-index: 99 !important;
}


.mdxeditor-root-contenteditable {
  font-size: 22px;
}


.loader {
  color: red;
  font-size: 20px;
  margin: 100px auto;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: load4 1.3s infinite linear;
  animation: load4 1.3s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
@-webkit-keyframes load4 {
  0%,
  100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}
@keyframes load4 {
  0%,
  100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}

